@import url(//netdna.bootstrapcdn.com/font-awesome/3.2.1/css/font-awesome.css);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #f4f4f4;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.search-container {
  z-index: 1;
  position: fixed;
  width: 100%;
  margin-top: -5%;
  opacity: 0.9;
  height: 70px;
  display: flex;
  padding-bottom: 2%;
  justify-content: space-between;
  border-bottom: 1px solid gray;
  background: white;
}

.logo {
  display: flex;
  width: 15%;
  margin: 0.5% 0 0 4%;
}

.logo i {
  margin: 5% 0;
  padding-right: 2%;
   color: #d6249f;
}

.logo h1 {
  font-family: "Grand Hotel", cursive;
  font-weight: 150;
  margin: 2%;
  height: 45px;
  padding-left: 3%;
  border-left: 1px solid black;
  color: #4267B2;
}

.search-form input {
  margin-top: 8%;
  background: white;
  width: 200px;
  height: 30px;
  border-color: black;
  border-radius: 10px;
  font-family: "FontAwesome", "<<Next Font for words>>";
}

.search-form input:focus {
  outline: none;
  border-color: #50BFE6; 
}

.options {
  width: 15%;
  margin: 1% 2% 0 1%;
  display: flex;
  justify-content: space-around;
}

.options i:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  color: #50BFE6;
}

.user p {
  margin: -10% -20%;
}
.comment-container {
  position: relative;
}

.comments {
  width: 97%;
  margin: 0 auto;
}
.comment {
  margin: 0 auto;
}

.comment-form {
  display: flex;
}

.comment-form input {
  width: 90%;
  border: 0px solid;
  padding-bottom: 1%;
  margin: 1% 0;
}

.comment-form input:focus {
  outline: none;
}

.comment-menu {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 10%;
  margin-bottom: 2%;
}

.menu-dot {
  margin: 2%;
  width: 5px;
  height: 5px;
  background: black;
  border-radius: 50%;
}

.likes {
  height: 30px;
  padding: 2%;
  font-weight: bold;
  display: flex;
}

.likes i {
  margin-right: 3%;
}

.likes i:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.liked {
  color: red;
}

.likes-counter {
  margin-left: 2%;
}

.comment-logo {
  margin: -0.5%;
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.post-container {
  width: 40%;
  margin: 5% auto;
  border: 1px solid gray;
  border-radius: 1%;
  background: white;
}

.post-header {
  display: flex;
}

.post-header img {
  border-radius: 50%;
  width: 5%;
  height: 5%;
  margin: 2%;
}

.post-body img {
  width: 100%;
  height: 500px;
}

* {
  box-sizing: border-box;
}

.App {
  width: 100%;
  margin: 0 auto;
}

.login-header {
  z-index: 1;
  position: fixed;
  width: 100%;
  margin-top: -5%;
  opacity: 0.9;
  height: 70px;
  display: flex;
  padding-bottom: 2%;
  justify-content: space-between;
  border-bottom: 1px solid gray;
  background: white;
}

.main-login-header {
  color: #d6249f;
}

.login-logo {
  width: 30%;
}

.login-header .user {
  margin: 1% 2.5% 0 0;
  color: #4267b2;
  cursor: pointer;
}

.login-form {
  display: flex;
  flex-direction: column;
  height: 100vh;
  margin-top: 5%;
  align-items: center;
}

.field {
  margin: 5% 0;
}

.field input {
  width: 100%;
  height: 40px;
  border-radius: 2%;
  border: 0px solid;
  border-bottom: 1.2px solid #4267b2;
}

.field input:focus {
  outline: none;
  border-color: #50bfe6;
}

.login-submit {
  /* padding: .3%; */
  text-align: center;
  background: white;
  color: #4267b2;
  font-weight: bold;
  border-radius: 13px;
  border: 3px solid #4267b2;
  /* border-bottom: 3px solid #4267b2; */
  cursor: pointer;
}

.login-submit:hover {
  color: white;
  border-color: white;
  background: #4267b2;
  -webkit-transform: scale(0.99);
          transform: scale(0.99);
}

.remember-me {
  display: flex;
  justify-content: space-between;
}

.login-div {
  border-radius: 2%;
  border: 2px solid #4267b2;
  width: 25%;
  padding: 2%;
  background: white;
}

